import Head from 'next/head'

import { generatePrime } from '../src/utils'

const primes = generatePrime(100)
export default () => (
  <div>
    <Head>
      <title>Prime table</title>
      <meta name="viewport" content="initial-scale=1.0, width=device-width" />
    </Head>
    <table>
      <tr>
        <th />
        {primes.map(x => (
          <th>{x}</th>
        ))}
      </tr>
      {primes.map(x => (
        <tr>
          <th>{x}</th>
          {primes.map(y => (
            <td>{x * y}</td>
          ))}
        </tr>
      ))}
    </table>
    <style jsx>{`
      table {
        margin: 0;
        padding: 0;
        color: #232323;
      }
      tr {
        margin: 0;
        padding: 0;
      }
      th {
        min-width: 15px;
        margin: 0;
        padding: 5px;
        text-align: center;
        border: 1px solid #232323;
        font-weight: bold;
      }
      td {
        min-width: 15px;
        margin: 0;
        padding: 5px;
        text-align: center;
        border: 1px solid #232323;
      }
    `}</style>
    <style global jsx>{`
      * {
        box-sizing: border-box;
      }
    `}</style>
  </div>
)
